<template>
  <div class="menu">
    <van-nav-bar title="菜单" left-text="返回" left-arrow @click-left="onClickLeft" />

    <div class="top"></div>

    <div class="main">
      <div class="aside">
        <div class="line"></div>
        <div
          @click="clickChange(item,index)"
          :class="{'act':isAct == index}"
          class="aside-item"
          v-for="(item, index) in asideData"
          :key="index"
        >{{item.typeDesc}}</div>
      </div>
      <div class="main-shop">
        <div
          @click="toDetail(item)"
          class="shop-item"
          v-for="(item,index) in showData"
          :key="index"
        >
          <div class="shop-img">
            <img v-lazy="item.smallImg" :src="item.smallImg" alt />
          </div>
          <div class="shop-content">
            <div class="title">{{item.name}}</div>
            <div class="en">{{item.enname}}</div>
            <div class="price">￥{{item.price}}</div>
          </div>
          <div class="shop-add">
            <span>+</span>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import Api from "../api/api";
export default {
  data() {
    return {
      isAct: -1,
      top: 0,
      type: null
    };
  },
  computed: {
    asideData() {
      return this.$store.getters["menuModule/asideData"];
    },
    showData() {
      return this.$store.state.menuModule.showData;
    }
  },

  mounted() {
    // 传入的参数
    this.type = this.$route.query.type || 1;
    // 初始化激化项
    let index = 0;
    for (let i = 0; i < this.asideData.length; i++) {
      if (this.asideData[i].type == this.type) {
        // this.isAct = i;
        index = i;
        break;
      }
    }

    let type = this.type;

    // 相对于点击一下切换按钮
    this.clickChange({ type }, index);
  },
  methods: {
    onClickLeft() {
      this.$router.go(-1);
    },
    // 点击切换事件
    clickChange(item, index) {
      // 拦截层
      if (index == this.isAct) {
        return;
      }

      this.isAct = index;
      let element = document.querySelector(".aside-item");
      let line = document.querySelector(".line");
      let h = element.offsetHeight;
      line.style.top = this.isAct * h + "px";

      // 简单修改换路径
      let type = item.type;
      if (this.$route.query.type != type) {
        if (type == 1) {
          type: null;
        }
        this.$router.push({ name: "Menu", query: { type } });
      }

      //开始加载中
      this.$toast.loading({
        duration: 0,
        message: "加载中...",
        forbidClick: true
      });

      //  做个判断
      let key = null;
      if (item.type == 1) {
        // 是热门产品
        key = "isHot";
      } else {
        key = "type";
      }

      Api.getTypeProducts({ key, value: item.type }).then(res => {
        this.$store.commit("menuModule/getShowData", res.data.result);
        this.$toast.clear();
      });
    },

    // 点击去详情页面
    toDetail(item) {
      this.$router.push({ name: "Detail", params: { pid: item.pid } });
    }
  }
};
</script>

<style lang="scss" scoped>
@import "@/assets/scss/util.scss";
.act {
  background-color: #fff;
}
.menu {
  height: 100%;
}
// 重设一下样式
/deep/ .van-nav-bar {
  .van-icon-arrow-left {
    color: #7f5839;
  }
  .van-nav-bar__text {
    color: #7f5839;
  }
}

.top {
  width: 100%;
  height: 200px;
  overflow: hidden;
  background: url("../assets/img/guangao.jpg") no-repeat;
  background-size: cover;
  background-position: center center;
}

.main {
  width: 100%;
  height: calc(100% - 296px);
  display: flex;
  .aside {
    width: 25%;
    background-color: #eee;
  }
  .main-shop {
    width: 75%;
  }
}

.aside {
  position: relative;
  .aside-item {
    height: 40px;
    line-height: 40px;
    text-align: center;
    font-size: 16px;
    border-bottom: 1px solid #999;
  }
  .line {
    position: absolute;
    height: 40px;
    width: 2px;
    background-color: #7f5839;
    left: 0;
    top: 0;
    transition: top 0.2s ease;
  }
}

.main-shop {
  height: 371px;
  overflow-y: auto;
  .shop-item {
    display: flex;
    width: calc(100% - 12px);
    height: 80px;
    margin: 5px;
    border: 1px solid #7f5839;
    .shop-img {
      width: 80px;
      img {
        width: 100%;
        display: block;
      }
    }
    .shop-content {
      width: calc(100% - 120px);
      padding: 0 10px;
      box-sizing: border-box;
      .title {
        font-size: 16px;
        height: 30px;
        line-height: 30px;
        font-weight: 600;
      }
      .en {
        font-size: 12px;
        height: 30px;
        line-height: 20px;
        font-weight: 600;
        @include ellipseOne();
      }
      .price {
        font-size: 16px;
        height: 20px;
        line-height: 20px;
        color: #7f5839;
      }
    }
    .shop-add {
      width: 40px;
      position: relative;
      span {
        position: absolute;
        width: 20px;
        height: 20px;
        border-radius: 50%;
        background-color: #7f5839;
        color: #fff;
        left: 50%;
        top: 50%;
        transform: translate(-50%, -50%);
        text-align: center;
        line-height: 20px;
        font-weight: 600;
        font-size: 18px;
      }
    }
  }
}
</style>